.backdrop {
  z-index: 99999;
  opacity: 1;
  background-color: #ffb8cae0;
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

@media screen and (max-width: 767px) {
  .backdrop {
    padding: 15px;
  }
}

@media screen and (max-width: 1279px) {
  .backdrop {
    overflow-y: scroll;
  }
}

.modal {
  background-color: #fff;
  border-radius: 15px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.5);
}

@media screen and (max-width: 767px) {
  .backdrop.is-hidden .modal {
    transform: scale(.5);
  }
}

@media screen and (min-width: 768px) {
  .modal {
    width: 70vw;
    height: auto;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 10%)scale(1);
  }
}

@media screen and (min-width: 1280px) {
  .modal {
    width: 40vw;
    height: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }
}

.modal__close-btn {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #21212133;
  border-radius: 50%;
