  border-color: green;
}

.buy__input:not(:placeholder-shown):focus:invalid {
  color: red;
  border-color: red;
}

.buy__icon {
  fill: currentColor;
  width: 18px;
  height: 18px;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
